import react,{ useState } from 'react';
import './Header.css';
import Logo from '../Logo.png';
import { FiMenu, FiX } from 'react-icons/fi';

function Header() {
    const [Click,setClick] = useState(false)
    const handleClcik = () => setClick(!Click);

    const ColseClick = () => setClick(!Click);
    console.log(Click);

    
    window.addEventListener('scroll', function(){
        const nav = document.querySelector('.header');
        nav.classList.toggle("active",window.scrollY > 0 );
    })


    window.addEventListener('scroll',function(){
        const indexselect = document.querySelector('#index');
        const aboutselect = document.querySelector('#about');
        const introselect = document.querySelector('#intro');
        const educatselect = document.querySelector('#educate');
        const workselect = document.querySelector('#worked');
        const certiselect = document.querySelector('#certi');

        if(window.scrollY >= 0 && window.scrollY <= 500){
            indexselect.classList.add("active");
        }else{
            indexselect.classList.remove("active");
        }

        if(window.scrollY >= 500 && window.scrollY <= 1200){
            console.log("500 - 1200");
            aboutselect.classList.add("active");

        }else{
            aboutselect.classList.remove("active");
        }

        if(window.scrollY >= 1600 && window.scrollY <= 2700){
            console.log("1200 - 2000");
            introselect.classList.add("active");

        }else{
            introselect.classList.remove("active");
        }

        if(window.scrollY >= 2701 && window.scrollY <= 3600){
            console.log("2701 - 3600");
            educatselect.classList.add("active");

        }else{
            educatselect.classList.remove("active");
        }

        if(window.scrollY >= 3601 && window.scrollY <= 3900){
            console.log("3601 - 3900");
            workselect.classList.add("active");

        }else{
            workselect.classList.remove("active");
        }

        if(window.scrollY >= 4500 && window.scrollY <= 5300){
            console.log("4500 - 5000");
            certiselect.classList.add("active");

        }else{
            certiselect.classList.remove("active");
        }



        // console.log(this.scrollY);
    })


    return (
        <div>
            <div className="header">
                <div className="navcon">
                      <div className="logocon">
                         <a href="#"><img src={Logo} className="logo"></img></a>
                      </div>
                    <div className={ Click ? ("nav-menu active") : ("nav-menu ")}>
                        <ul>
                            <li onClick={ColseClick} id="index">
                                <div className="overlay">
                                      <div className="arrow"></div>
                                </div>
                                <a href="#indexsec">หน้าหลัก</a>
                            </li>
                            <li onClick={ColseClick} id="about">
                                <div className="overlay">
                                      <div className="arrow"></div>
                                </div>
                                <a href="#aboutsec">เกี่ยวกับ</a>
                            </li>
                            <li onClick={ColseClick} id="intro">
                                <div className="overlay">
                                      <div className="arrow"></div>
                                </div>
                                <a href="#introsec">แนะนำตนเอง</a>
                            </li>
                            <li onClick={ColseClick} id="educate">
                                <div className="overlay">
                                      <div className="arrow"></div>
                                </div>
                                <a href="#educatesec">การศึกษา</a>
                            </li>
                            <li onClick={ColseClick} id="worked">
                                <div className="overlay">
                                      <div className="arrow"></div>
                                </div>
                                <a href="#worksec">ผลงาน</a>
                            </li>

                            <li onClick={ColseClick} id="certi">
                                <div className="overlay">
                                      <div className="arrow"></div>
                                </div>
                                <a href="#certificate">ประกาศนียบัตร</a>
                            </li>
                            <li onClick={ColseClick} id="footersec">
                                <div className="overlay">
                                      <div className="arrow"></div>
                                </div>
                                <a href="#footer">ติดต่อ</a>
                            </li>

                        </ul>
                        <div className="navbar-toggle" onClick={ handleClcik }>
                            { Click ? (<FiX className="bar" />) : (<FiMenu className="bar"/>)}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Header;
